import Vue from 'vue'
import Vuex from 'vuex'
import VuexAlong from 'vuex-along'
Vue.use(Vuex)

//state
if(window.location.hash.indexOf('login')>0){
   window.clearVuexAlong;
}

var stateTemp={
  bottomShow: true,
    //
    recordDetail:{},
    //productDetail
    orderDetail:{
      productDetail:{},
      customerDetail:{},
      type:'',
    },
    //
    orderRss:{
       //
       productId:'',//id
       bankAccount:'',//
       bankBranch:'',//
       bookingBalance:'',//
       feeBalance:'',//
       paymentBlance:'0.00',//
       paymentDate:'',//
       empJobnumber:'', //
       customerName:'',//
       customerId:'',//id
       pinCode:'',//
       riskMismatch:{},//
       paymentFile:{},//
       identificationFile:{},//
       bankcardFile:{},//
       otherFiles:[],//
       startId:null,
       customerFrom:'',//
      referPin:'',//
       //
       accompanyPersonName:'',//
       cusLevelName:'',//
    },
  modifySuggestPrdList:[],
  chooseDataList: [],
  portfolio_cfg: {}
};
var stateString=JSON.stringify(stateTemp);
export default new Vuex.Store({
    state:JSON.parse(stateString),
    mutations:{
      storePortFolioCfg(state, obj) {
        state.portfolio_cfg = obj;
      },
      changeChooseDataList(state,obj) {
        state.chooseDataList = obj;
      },
      changeBottomShow(state,value){
        state.bottomShow = value;
      },
      changeRecordDetail(state,obj){
        if(Object.keys(obj).length==0){
          state.recordDetail={};
        }
        for(var x in obj){
          if(obj[x]!=undefined){
            state.recordDetail[x]=obj[x];
          }
        }
      },
      changeOrderDetail(state,obj){
        if(Object.keys(obj).length==0){
          state.orderDetail={
            productDetail:{},
            customerDetail:{},
            type:{},
          };
        }
        if(obj.productDetail!=undefined){
          state.orderDetail.productDetail=obj.productDetail;
        }
        if(obj.customerDetail!=undefined){
          state.orderDetail.customerDetail=obj.customerDetail;
        }
        if(obj.type!=undefined){
          state.orderDetail.type=obj.type;
        }
      },
      changeOrderRss(state,obj){
        if(Object.keys(obj).length==0){
          state.orderRss={
            //
            productId:'',//id
            bankAccount:'',//
            bankBranch:'',//
            bookingBalance:'',//
            feeBalance:'',//
            paymentBlance:'0.00',//
            paymentDate:'',//
            empJobnumber:'', //
            customerName:'',//
            customerId:'',//id
            pinCode:'',//
            riskMismatch:{},//
            paymentFile:{},//
            identificationFile:{},//
            bankcardFile:{},//
            otherFiles:[],//
            startId:null,
            customerFrom:'',//
            referPin:'',//
            //
            accompanyPersonName:'',//
            cusLevelName:'',//
         };
        }
        for(var x in obj){
          if(obj[x]!=undefined){
            state.orderRss[x]=obj[x];
          }
        }
      },
    },
    plugins:[VuexAlong]
})




