import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index'
import func from '@/public/func'
import api from '@/api/api'
import utils from '@/utils/utils'
import powerKey from '@/utils/powerKey'
import { resolve } from 'path';

Vue.use(Router)
const routers = [
  {path: '/login', name: 'login', component: '/login'},//
  {path: '/autoLogin', name: 'autoLogin', component: '/autoLogin'},//
  {path: '/home', name: 'home', component: '/indexNew'},//
  {path: '/customer', name: 'customer', component: '/customer'},//
  {path: '/product', name: 'product', component: '/product'},//
  {path: '/personal', name: 'personal', component: '/personal'},//
  {path: '/achievement', name: 'achievement', component: '/achievement'},//
  {path: '/product/details', name: 'productDetails', component: '/product/details'},//
  {path: '/product/order', name: 'order', component: '/product/order'},//
  {path: '/product/orderCustomer', name: 'orderCustomer', component: '/product/orderCustomer'},//-
  {path: '/product/orderRisk', name: 'orderRisk', component: '/product/orderRisk'},//-
  {path: '/product/orderRss', name: 'orderRss', component: '/product/orderRss'},//-
  {path: '/product/report', name: 'productReport', component: '/product/report'},//
  {path: '/product/recommondProductInfo', name: 'recommondProductInfo', component: '/product/recommondProductInfo'},//
  {path: '/customer/add', name: 'customerAdd', component: '/customer/add'},//
  {path: '/customer/details', name: 'customerDetails', component: '/customer/details'},//
  {path: '/customer/recordList', name: 'recordList', component: '/customer/recordList'},//
  {path: '/customer/recordDetail', name: 'recordDetail', component: '/customer/recordDetail'},//
  {path: '/customer/investment', name: 'customerInvestment', component: '/customer/investment'},//
  {path: '/customer/redeemAdd', name: 'customerRedeemAdd', component: '/customer/redeemAdd'},//
  {path: '/customer/redeem', name: 'customerRedeem', component: '/customer/redeem'},//
  {path: '/customer/info', name: 'customerInfo', component: '/customer/info'},//
  {path: '/message', name: 'message', component: '/customer/message'},//
  {path: '/personal/declarationForm', name: 'declarationForm', component: '/personal/declarationForm'},//
  {path: '/personal/contract', name: 'contract', component: '/personal/contract'},//
  {path: '/personal/contractAdd', name: 'contractAdd', component: '/personal/contractAdd'},//
  {path: '/personal/contractAddSuccess', name: 'contractAddSuccess', component: '/personal/contractAddSuccess'},//
  {path: '/customer/authentication', name: 'authentication', component: '/customer/authentication'},//
  {path: '/customer/questions', name: 'questions', component: '/customer/questions'},//
  {path: '/customer/evaluation', name: 'evaluation', component: '/customer/evaluation'},//
  {path: '/customer/evaluationNo', name: 'evaluationNo', component: '/customer/evaluationNo'},//
  {path: '/customer/asset', name: 'asset', component: '/customer/asset'},//
  {path: '/customer/selectCustomer', name: 'selectCustomer', component: '/customer/selectCustomer'},//
  {path:'/customer/customerSearch',name:'customerSearch',component:'/customer/customerSearch'},//
  {path:'/product/productSearch',name:'productSearch',component:'/product/productSearch'},//
  {path:'/customer/uploadAttachment',name:'uploadAttachment',component:'/customer/uploadAttachment'},//
  {path:'/product/customerCertification',name:'customerCertification',component:'/product/customerCertification'},//
  {path:'/product/bank',name:'bank',component:'/product/bank'},//
  {path:'/product/orderResult',name:'orderResult',component:'/product/orderResult'},//
  {path:'/staff/staff',name:'staff',component:'/staff/staff'},//
  {path: '/product/productDetail', name: 'productDetail', component: '/product/productDetail'},//
  {path: '/product/fee', name: 'fee', component: '/product/fee'},//
  {path: '/product/moreDetail', name: 'moreDetail', component: '/product/moreDetail'},//
  {path: '/product/netValueList', name: 'netValueList', component: '/product/netValueList'},//
  {path: '/test', name: 'test', component: '/product/test'},//test
  {path: '/product/redeem', name: 'redeem', component: '/product/redeem'},//
  {path: '/product/redeemResult', name: 'redeemResult', component: '/product/redeemResult'},//
  {path: '/customer/assetsList', name: 'assetsList', component: '/customer/assetsList'},//
  {path: '/customer/assetAllocate/assetAllocationList', name: 'assetAllocationList', component: '/customer/assetAllocate/assetAllocationList'},//
  {path: '/customer/assetAllocate/assetAllocateDetail/assetAllocationReportDetail', name: 'assetAllocationReportDetail', component: '/customer/assetAllocate/assetAllocateDetail/assetAllocationReportDetail'},//
  {path: '/customer/assetAllocate/modifyBasicInfo', name: 'modifyBasicInfo', component: '/customer/assetAllocate/modifyBasicInfo'},//
  {path: '/customer/assetAllocate/assetAllocateDetail/checkAssetClassifyDetail', name: 'checkAssetClassifyDetail', component: '/customer/assetAllocate/assetAllocateDetail/checkAssetClassifyDetail'},//
  {path: '/customer/assetAllocate/modifyInvestSuggest', name: 'modifyInvestSuggest', component: '/customer/assetAllocate/modifyInvestSuggest'},//
  {path: '/customer/assetAllocate/addSuggestProduct', name: 'addSuggestProduct', component: '/customer/assetAllocate/addSuggestProduct'},//
  {path: '/personal/manageAssetsList', name: 'manageAssetsList', component: '/personal/manageAssetsList'},//
  {path: '/personal/editSalesManBusinessCard', name: 'editSalesManBusinessCard', component: '/personal/editSalesManBusinessCard'},//
  {path: '/personal/salesManBusinessCardInfo', name: 'salesManBusinessCardInfo', component: '/personal/salesManBusinessCardInfo'},//
  {path: '/customer/customerTalk', name: 'customerTalk', component: '/customer/customerTalk'},//
  {path: '/customer/birthdayRemind', name: 'birthdayRemind', component: '/customer/birthdayRemind'},//
  {path: '/marketShare/shareRecords', name: 'shareRecords', component: '/marketShare/shareRecords'}, // -
  {path: '/marketShare/activityList', name: 'activityList', component: '/marketShare/activityList'}, // -
  {path: '/marketShare/activityInfo', name: 'activityInfo', component: '/marketShare/activityInfo'}, // -
  {path: '/marketShare/articleInfo', name: 'articleInfo', component: '/marketShare/articleInfo'}, // -
  {path: '/marketShare/posterInfo', name: 'posterInfo', component: '/marketShare/posterInfo'}, // -
  {path: '/marketShare/sharedRecordInfo', name: 'sharedRecordInfo', component: '/marketShare/sharedRecordInfo'}, // -
  {path: '/marketShare/userVisitRecords', name: 'userVisitRecords', component: '/marketShare/userVisitRecords'} // -
]
let newRouters = [
  { path: '/', redirect: '/login' }
]
for (let i = 0; i < routers.length; i++){
  newRouters.push({path: routers[i].path, name: routers[i].name,component:resolve=>require(['@/pages' + routers[i].component],resolve)})
}

const router=new Router({
  routes: [
    ...newRouters
  ]
})

//,arg
router.beforeEach((to,from,next)=>{
  if(from.name === 'activityList') {
    // if(to.query.itemIndex === '0'){
    //   next()
    // }else{
    //   next({path: '/marketShare/activityList?itemIndex=0'})
    // }
    // return;

  }
  // if(from.name === 'posterInfo' && to.name === 'activityList') {
  //   if(to.query.itemIndex === '1'){
  //     next()
  //   }else{
  //     next({path: '/marketShare/activityList?itemIndex=1'})
  //   }
  //   return;
  // }
  // if(from.name === 'activityInfo' && to.name === 'activityList') {
  //   if(to.query.itemIndex === '2'){
  //     next()
  //   }else{
  //     next({path: '/marketShare/activityList?itemIndex=2'})
  //   }
  //   return;
  // }
    //loginarg
    if(to.name=="login" || to.name=="autoLogin"){
      next();
      return;
    }
  if(utils.userInfo.userPowerKey.length > 0 ) {
    if (!utils.userInfo.checkUserPowerKey(powerKey.messageListKey) && to.name === "home" || !utils.userInfo.checkUserPowerKey(powerKey.customerListKey) && to.name === "customer" ||  !utils.userInfo.checkUserPowerKey(powerKey.productListKey) && to.name === "product") {
      next( "/personal");
      return;
    }
  }
    if(to.query.token) {
      const searchParams = to.query.token;
      if (searchParams) {
        localStorage.setItem("token", searchParams);
          func.ajaxPost(api.userInfo.getUserInfo, {}, res=> {
            if (res.data.code === 200) {
              if (!res.data.data) {
                return false
              }
              let data = JSON.parse(res.data.data);
              if (!data.userPowerKey) {
                return false;
              }
              // 
              localStorage.setItem('pinCode', data.pinCode);
              localStorage.setItem('oriPinCode', data.oriPinCode);
              localStorage.setItem('userName', encodeURIComponent(data.trueName));
              // 
              localStorage.setItem('isSales', data.isSales);
              utils.userInfo.userPowerKey = data.userPowerKey.split(',');
              // 
              if(!utils.userInfo.checkUserPowerKey(powerKey.messageListKey)){
                next( "/personal");
                return;
              }
            };
          })
      }
    }
    if(utils.arg.data.length!=0){
      next();
    }else{
      func.ajaxPost(api.arg.getArgAll,{},res=>{
        utils.arg.data = [];
        if (res.data.code === 200) {
             if (res.data.data) {
               let data=res.data.data;
                for (var i = 0; i < data.length; i++) {
                    var item = data[i];
                    utils.arg.data.push({
                        group: item.argGroup,
                        code: item.argCode,
                        text: item.argText,
                        remark: item.argRemark
                    })
                }
                utils.arg.init();
                next();
            }
        }
      })
    }
  if(utils.userInfo.userPowerKey && utils.userInfo.userPowerKey.length !== 0){
    next();
  }else{
    func.ajaxPost(api.userInfo.getUserInfo, {}, res=> {
      if (res.data.code === 200) {
        if (!res.data.data) {
          return false
        }
        let data = JSON.parse(res.data.data);
        if (!data.userPowerKey) {
          return false;
        }
        // 
        localStorage.setItem('pinCode', data.pinCode);
        utils.userInfo.userPowerKey = data.userPowerKey.split(',')
        next();
      };
    })
  }

})

//
export default router



